body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  color: #525252;
}

.App-link {
  color: #61dafb;
}

.Header {
  color: #636363;
}

.Header h2 {
  margin: 10px 0px;
}

.GameBoard {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.GameBoardRow {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.GameBoardSquare {
  width: 100px;
  height: 100px;
  line-height: 100px;
  color: transparent;
  font-size: 48px;
  font-weight: bold;
  background-color: rgb(237, 0, 0, .5);
  border-radius: 10px;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.GameBoardSquare:hover {
  background-color: rgb(237, 0, 0);
  color: #f1d6d6;
}

.GameBoardSquare__owned {
  pointer-events: none;
  background-color: rgb(237, 0, 0);
  color: #f1d6d6;
}

.NoHover:hover {
  background-color: rgb(237, 0, 0, .5);
  color: transparent;
}

.InfoBar {
  width: 300px;
  display: flex;
  justify-content: space-between;
}

.ScoreBoard {
  position: fixed;
  pointer-events: none;
  top: 50px;
  right: 20px;
}

.ScoreBoard > h3 {
  font-size: 16px;
  font-weight: bold;
}
